<template>
  <div class="list">
    <!-- loadig组件 -->
    <loading v-if="!items.length"></loading>
    <ul class="con">
      <li v-for="item in items">
        <a :href="item.wp" :download="item.id+url">
          <img v-lazy="item.img">
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
//引入loading组件
import loading from '@/components/Loading';


export default {
  name: 'wallpaper',
  data () {
    return {
      url: '.jpg',
    }
  },
  //接收 items
  props:['items'],
  // 加载组件
  components: {
    loading
  },
}
</script>

<style scoped lang="scss">
// 引入公共样式
@import '@/assets/sass/base.scss';
// 引入列表样式
@import '@/assets/sass/wallStyle.scss';

</style>



