<template>
  <div class="city">
    <div v-for="city in cities">
      <div class="name">
        <h1>{{city.name}}</h1>
        <ul>
          <a href="" v-for="a in city.items">
            <router-link tag="li" :to="{
              path: '/weather',
              query:{
                cityid: a.cityid
              }
            }">
              {{a.city}}
            </router-link>            
          </a>

        </ul>

      </div>
    </div>
  </div>


</template>

<script>
//引入 城市数据
import cities from '@/Json/cities.json';

export default {
  name: 'City',
  data () {
    return {
      cities:[]
    }
  },
  mounted: function() {
    this.cities = cities.cities;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入公共样式
  @import '../../assets/sass/base.scss';
  .city {
    width: 100%;
    position: absolute;
    background: #9FBDFB;
    z-index: 999;
  }
</style>



