<template>
  <div class="news">
    <nav class="top swiper-container">
      <ul class="swiper-wrapper">
        <router-link to="/news/hot" class="swiper-slide" tag="li">热点</router-link>
        <router-link to="/news/society" class="swiper-slide" tag="li">社会</router-link>
        <router-link to="/news/entertainment" class="swiper-slide" tag="li">娱乐</router-link>
        <router-link to="/news/ask" class="swiper-slide" tag="li">问答</router-link>
        <router-link to="/news/tech" class="swiper-slide" tag="li">科技</router-link>
        <router-link to="/news/sport" class="swiper-slide" tag="li">体育</router-link>
        <router-link to="/news/finance" class="swiper-slide" tag="li">财经</router-link>
        <router-link to="/news/military" class="swiper-slide" tag="li">军事</router-link>
        <router-link to="/news/world" class="swiper-slide" tag="li">国际</router-link>
      </ul>
    </nav>   
    <router-view></router-view> 
  </div>
</template>

<script>
export default {
  name: 'News',
  data () {
    return {

    }
  }
}
// var swiper = new Swiper('.swiper-container', {
//   slidesPerView: 4,
//   spaceBetween: 30,
//   centeredSlides: true,
// });
</script>

<style scoped lang="scss">
  // 引入公共样式
  @import '../assets/sass/base.scss';
  nav.top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: rem(72px);
    background: #F4F5F6;
    line-height: rem(72px);
    ul {
      // display: flex;
      overflow: hidden;
      white-space: nowrap;
      li {
        display: inline-block;
        margin: 0 rem(20px);
        font-size: rem(30px);
        color: #505050;
        &.router-link-exact-active {
          color: #f85959;
        }
      }
      
    }

  }
</style>




