<template>
  <div class="ask">
    <news-top :news="news"></news-top>
  </div>
</template>

<script>
//引入主题列表的组件
import newsTop from '@/components/NewsTop/List';
//引入jquery
import $ from 'jquery'

export default {
  name: 'Ask',
  data () {
    return {
      //主题列表
      news: []
    }
  },
  // 钩子函数
  mounted: function() {
    $.ajax({
      url: 'http://is.snssdk.com/api/news/feed/v51/?tag=question_and_answer&count=20',
      type: 'GET', 
      dataType: 'JSONP', 
      success: (res)=>{ 
        this.items = res.data;
        this.news = [];
        for(var i = 0; i<this.items.length; i++) {
          this.a = eval('(' + this.items[i].content + ')');//把字符串转换为对象
          this.news.push(this.a);
        }
      } 
    })
  },
  // 加载组件
  components: {
    newsTop
  }
}
</script>

<style scoped lang="scss">
  // 引入公共样式
  @import '../../assets/sass/base.scss';

  //引入列表样式
  @import '../../assets/sass/style.scss';

</style>







