<template>
  <div class="girl">
    <wall-top :items="items"></wall-top>
    <!-- 上下滑动的组件 -->
    <infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading>
  </div>
</template>

<script>
//引入主题列表的组件
import wallTop from '@/components/WallpaperTop/List';
//引入Jquery
import $ from 'jquery'
// 引入 分页滑动组件
import InfiniteLoading from 'vue-infinite-loading';


export default {
  name: 'Girl',
  data () {
    return {
      items: [],
      // 默认显示 第一页
      skip:0,
      isShow:true,
      num: 10
    }
  },
  // 钩子函数
  mounted: function() {
    $.getJSON("http://service.picasso.adesk.com/v1/vertical/category/4e4d610cdf714d2966000000/vertical",{limit: 10,order: 'hot',skip:this.skip}, (data)=>{
        this.items = data.res.vertical;
        // console.log(data.res.vertical)
    });
  },
  methods: {
    infiniteHandler($state) {
      setTimeout(() => {
        this.skip=this.skip+10;
        $.getJSON("http://service.picasso.adesk.com/v1/vertical/category/4e4d610cdf714d2966000000/vertical",{limit: 10,order: 'hot',skip:this.skip}, (data)=>{
            this.items = this.items.concat(data.res.vertical);
            if(data.res.vertical.length ==0){
              this.isShow = false;
              // 结束了
              return;
            }
            // 再次调用请求的函数
            $state.loaded();

        });

      }, 500);
    },
  },
  // 加载组件
  components: {
    wallTop,InfiniteLoading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// 引入公共样式
@import '@/assets/sass/base.scss';
// 引入列表样式
@import '@/assets/sass/wallStyle.scss';

</style>



