<template>
  <div class="weather">
    <!-- loadig组件 -->
    <loading v-if="!info1"></loading>
    <div class="select">
      <select id="cities" v-model="val">

        <optgroup v-for="a in cities" :label="a.name">
          <option v-for="b in a.items" :value="b.city">{{b.city}}</option>
        </optgroup>

      </select>
    </div>
    <!-- <router-link to="/Weather/City" tag="a">{{city.city}}</router-link> -->
    <router-view></router-view> 

    <div class="info">
      <div class="wendu">
        <p>
          {{wendu}}
          <span>℃</span>
        </p>
      </div>
      <div class="tianqi">
        <p>{{info1.type}}</p>
      </div>
      <div class="mm">
        <p>最{{info1.high}}</p>
        <p>最{{info1.low}}</p>
      </div>
    </div>

    <div class="yvbao">
      <p class="yvbao_title">天气预报</p>
      <ul>
        <li v-for="item in info2">
          <p>{{item.date}}</p>
          <p>最{{item.high}}</p>
          <p>最{{item.low}}</p>
          <p>{{item.type}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
//引入 城市数据
import cities from '@/Json/cities.json';
//引入loading组件
import loading from '@/components/Loading';
//引入jquery
import $ from 'jquery'

export default {
  name: 'Weather',
  data () {
    return {
      cities: [],
      val: '广州',
      info1: '',
      info2: '',
      city: [],
      wendu: '',
    }
  },
  created() {
    // this.val = this.$route.query.cityid;
    // console.log(this.val);
  },
  // 加载组件
  components: {
    loading
  },
  // 钩子函数
  mounted: function() {
    this.cities = cities.cities;

    $.getJSON("https://www.apiopen.top/weatherApi",{city:this.val}, (data)=>{
        // console.log(data.res.vertical)
        // console.log(data.data)
        this.wendu = data.data.wendu;
        this.info1 = data.data.forecast[0];
        // 天气预报
        this.info2 = data.data.forecast;
    });



 
  },
  watch: {
    val(val,oldval) {
    $.getJSON("https://www.apiopen.top/weatherApi",{city: val}, (data)=>{
        // console.log(data.res.vertical)
        // console.log(data.data)
        this.wendu = data.data.wendu;
        this.info1 = data.data.forecast[0];
        // 天气预报
        this.info2 = data.data.forecast;
    });  


    }
  },
}
</script>

<style scoped lang="scss">
  // 引入公共样式
  @import '../assets/sass/base.scss';

  // 引入样式
  @import '../assets/sass/weather.scss';




</style>



