import Vue from 'vue'
import App from './App'
import router from './router'

//引入懒加载组件
import VueLazyload from 'vue-lazyload'
//懒加载配置
Vue.use(VueLazyload, {
	preLoad: 1.3,
	//请求失败时加载的图片
	error: require('../static/images/mo.jpg'),
	//加载中的图片
	loading: require('../static/images/timg.gif'),
	attempt: 1
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



