<template>
  <div class="list">
    <!-- loadig组件 -->
    <loading v-if="!news.length"></loading>
    <!-- 主题列表 -->
    <ul class="list">
      <li v-for="item in news" v-if="item.middle_image">
        <a :href="item.share_url">
          <div class="left">
            <h3>{{item.title}}</h3>
            <span>{{item.media_name}}</span>
          </div>
          <div class="right">
            <img v-if="item.middle_image" v-lazy="item.middle_image.url">
            <img v-else="item.image_list[0]" v-lazy="item.image_list[0].url">
          </div>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
//引入loading组件
import loading from '@/components/Loading';

export default {
  name: 'List',
  data () {
    return {
    }
  },
  //接收 items
  props:['news'],
  // 加载组件
  components: {
    loading
  }
}
</script>



<style scoped lang="scss">
  // 引入公共样式
  @import '../../assets/sass/base.scss';
  //引入列表样式
  @import '../../assets/sass/style.scss';
</style>



