<template>
  <div id="app">
    <router-view/>
    <nav class="footer">
      <ul>
        <router-link to="/news" tag="li" :class="resPath=='news'?'mo':''">
          <span class="iconfont icon-xinwen"></span>
          <p>新闻</p>
        </router-link>
        <router-link to="/weather" tag="li" :class="resPath=='weather'?'mo':''">
          <span class="iconfont icon-tianqi"></span>
          <p>天气</p>
        </router-link>
        <router-link to="/wallpaper" tag="li" :class="resPath=='wallpaper'?'mo':''">
          <span class="iconfont icon-tupian"></span>
          <p>壁纸</p>
        </router-link>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  //计算属性
  computed: {
    resPath:function() {
      var arr = this.$route.path.split('/');
      return arr[1];
    }
  }
}
</script>


<style lang="scss">
  // 引入公共样式
  @import './assets/sass/base.scss';
  
  nav.footer {
    width: 100%;
    height: rem(100px);
    background: #fff;
    border-top: rem(1px) solid #ccc;
    position: fixed;
    z-index: 9999;
    bottom: 0;
    left: 0;
    ul {
      display: flex;
      li {
        width: 33.333%;
        color: #999;
        text-align: center;
        span {
          display: block;
          font-size: rem(50px);
        }
        p {
          font-size: rem(30px);
        }
        &.mo {
          color: red;
          border-radius: rem(15px);
        }
      }
    }
  }
</style>



