import Vue from 'vue'
import Router from 'vue-router'

// 一级路由
import Wallpaper from '@/components/Wallpaper'
import Weather from '@/components/Weather'
import News from '@/components/News'
// 新闻二级路由
import Hot from '@/components/NewsTop/Hot'
import Society from '@/components/NewsTop/Society'
import Entertainment from '@/components/NewsTop/Entertainment'
import Ask from '@/components/NewsTop/Ask'
import Tech from '@/components/NewsTop/Tech'
import Sport from '@/components/NewsTop/Sport'
import Finance from '@/components/NewsTop/Finance'
import Military from '@/components/NewsTop/Military'
import World from '@/components/NewsTop/World'

//壁纸二级路由
import Girl from '@/components/WallpaperTop/Girl'
import Animation from '@/components/WallpaperTop/Animation'
import Emotion from '@/components/WallpaperTop/Emotion'
import Game from '@/components/WallpaperTop/Game'
import Text from '@/components/WallpaperTop/Text'
import Vision from '@/components/WallpaperTop/Vision'
import Celebrity from '@/components/WallpaperTop/Celebrity'

// 天气二级路由
import City from '@/components/WeatherTop/City'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/wallpaper',
      component: Wallpaper,
      children: [
        {
          path: 'girl',
          component: Girl
        },
        {
          path: 'animation',
          component: Animation
        },
        {
          path: 'emotion',
          component: Emotion
        },
        {
          path: 'game',
          component: Game
        },
        {
          path: 'text',
          component: Text
        },
        {
          path: 'vision',
          component: Vision
        },
        {
          path: 'celebrity',
          component: Celebrity
        },
        // 路由跳转
        {
          path: '/',
          redirect: 'animation'
        }
      ]
    },
    {
      path: '/weather',
      component: Weather,
      children: [
        {
          path: 'city',
          component: City
        }
      ]
    },
    {
      path: '/news',
      component: News,
      children: [
        {
          path: 'hot',
          component: Hot
        },
        {
          path: 'society',
          component: Society
        },
        {
          path: 'entertainment',
          component: Entertainment
        },
        {
          path: 'ask',
          component: Ask
        },
        {
          path: 'tech',
          component: Tech
        },
        {
          path: 'sport',
          component: Sport
        },
        {
          path: 'finance',
          component: Finance
        },
        {
          path: 'military',
          component: Military
        },
        {
          path: 'world',
          component: World
        },
        //路由跳转
        {
          path: '/*',
          redirect: 'hot'
        }
      ]
    }
  ]
})



